"use client";

import { useEffect, useState } from "react";
import api from "@/lib/axios";
import { AxiosError } from "axios";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { useRouter } from "next/navigation";

interface Bundle {
    id: number;
    network: string;
    name: string;
    price: number;
}

export default function NewOrderPage() {
    const [bundles, setBundles] = useState<Bundle[]>([]);
    const [selectedBundle, setSelectedBundle] = useState<string>("");
    const [phone, setPhone] = useState("");
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState("");
    const router = useRouter();

    useEffect(() => {
        api.get('/products').then(res => setBundles(res.data));
    }, []);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setLoading(true);
        setError("");

        try {
            await api.post('/orders', {
                bundle_id: selectedBundle,
                recipient_phone: phone
            });
            router.push('/dashboard/orders');
        } catch (err) {
            const e = err as AxiosError<{ message: string }>;
            setError(e.response?.data?.message || "Order failed");
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="max-w-2xl mx-auto">
            <Card>
                <CardHeader>
                    <CardTitle>Place New Order</CardTitle>
                </CardHeader>
                <CardContent>
                    <form onSubmit={handleSubmit} className="space-y-6">
                        <div className="space-y-2">
                            <label className="text-sm font-medium">Select Bundle</label>
                            <Select onValueChange={setSelectedBundle} required>
                                <SelectTrigger>
                                    <SelectValue placeholder="Select a bundle" />
                                </SelectTrigger>
                                <SelectContent>
                                    {bundles.map((bundle) => (
                                        <SelectItem key={bundle.id} value={String(bundle.id)}>
                                            {bundle.network} - {bundle.name} (GHS {bundle.price})
                                        </SelectItem>
                                    ))}
                                </SelectContent>
                            </Select>
                        </div>

                        <div className="space-y-2">
                            <label className="text-sm font-medium">Recipient Phone</label>
                            <Input
                                placeholder="024xxxxxxx"
                                value={phone}
                                onChange={(e) => setPhone(e.target.value)}
                                required
                            />
                        </div>

                        {error && <p className="text-red-500">{error}</p>}

                        <Button type="submit" disabled={loading} className="w-full">
                            {loading ? "Processing..." : "Place Order"}
                        </Button>
                    </form>
                </CardContent>
            </Card>
        </div>
    );
}
